import { NavLink } from "react-router-dom";
import './Footer.css'
function  Footer() {

    function IsCartinit(){
        if (localStorage.getItem('cart-items') === null){
            return false
        }
        return true
    }
    
    const cartitemLength = IsCartinit() ? localStorage.getItem('cart-items').split(',').length : 0;
    return ( <>
    <div id="Footer" >
<NavLink className={({ isActive }) => isActive ? "active-header" : "unactive-header"}  to="/e-commerce01"><i className="bi bi-house"></i></NavLink>
<NavLink className={({ isActive }) => isActive ? "active-header" : "unactive-header"}  to="/my_cart"><i className="bi bi-cart"></i><span className='notif-small' id='Shopping-cart-Content'>{cartitemLength}</span></NavLink>
<NavLink className={({ isActive }) => isActive ? "active-header" : "unactive-header"}  to="/about_us"><i className="bi bi-telephone"></i></NavLink>
<NavLink className={({ isActive }) => isActive ? "active-header" : "unactive-header"}  to="/profile"><i className="bi bi-person"></i></NavLink>

    </div>
    </> );
}

export default Footer ;