import React from "react";
import { useParams } from "react-router-dom";
import { ProductArr } from "../components/data";
import Product from "../components/Product";
function SearchResult() {
    let params = useParams()
    const itemsArr = []
    for(let product of ProductArr){
        // console.log(product);
        var pname = product.name.toLowerCase()
        var squery = params.slug.toLowerCase()
        // console.log(pname,squery);
        if(pname.includes(squery)){
            itemsArr.push(product)
        }}

        const Products = itemsArr.map((ProductJson) =>
        <Product key={ProductJson.id} {...ProductJson}  />
   );
    return ( <div className="Viewing-Component">
    <h4>Search Results for {params.slug}</h4>
    <div className="View-Category-items" >
        {Products}
    </div>
    </div> );
}

export default SearchResult;