import { Link } from "react-router-dom";
import './AddProduct.css'
import $ from "jquery";
import { useEffect } from "react";

export function textareaElemUI(){
	$('textarea').on('click', function(){
	  $(this).height(1);
	  var totalHeight = $(this).prop('scrollHeight') - parseInt($(this).css('padding-top')) - parseInt($(this).css('padding-bottom'));
	  $(this).height(totalHeight);
  })
	  $('textarea').on('keydown', function(e){
		if(e.which === 13) {e.preventDefault();}
	}).on('input', function(){
		$(this).height(1);
		var totalHeight = $(this).prop('scrollHeight') - parseInt($(this).css('padding-top')) - parseInt($(this).css('padding-bottom'));
		$(this).height(totalHeight);
	})
  
	$('textarea').click();
  }
function AddProduct() {
	useEffect(() => {
	  textareaElemUI()
	
	//   return () => {
	// 	second
	//   }
	}, [])
	
    return ( <>
    <div className="adminviewproduct row">
                <span className='actiontop'>
                    <Link to={'/admin_dashboard/products'} ><i class="bi bi-x"></i></Link>
                
                <div className='random-2-btn' >
                    <button className='edit' ><i className="bi bi-plus-square"></i> <span>Add Product</span> </button>

                </div>
                </span>
                    <div className="add_product-div">
					<div className="images">
						
						
					</div>
					<div className="details">
						<div><label htmlFor="">Name</label><textarea type="text" /></div>
						<div><label htmlFor="">Detail</label><textarea type="text" /></div>
						<div><label htmlFor="">Mrp</label><textarea type="text" /></div>
						<div><label htmlFor="">Offer Price</label><textarea type="text" /></div>
						<div><label htmlFor="">Stocked</label><textarea type="text" /></div>
						{/* <div><label htmlFor="">Stocked</label><textarea type="text" /></div> */}
						
						

					</div>
                    </div>
						<div className="switch-two" ><div>Images</div><div>Details</div></div>
	</div>
    </> );
}

export default AddProduct;