import './ProductAn.css'
import ProductDetailsRow from '../componentsadmin/ProductDetailsRow';
import {Link,Route,Routes} from 'react-router-dom'
import AddProduct from '../componentsadmin/AddProduct';
import ViewProduct from '../componentsadmin/ViewProduct';
import { useState } from 'react';
import { CategoriesArr,ProductArr } from '../components/data';
import AddCategory from '../componentsadmin/AddCategory';
function ProductsAn() {
    const [ProductRow, SetProductRow] = useState(ProductArr.map((product)=>
        <ProductDetailsRow key={product.id} {...product} />
    ))

    const [Categories, setCategories] = useState(CategoriesArr.map((category)=>
    <option value={category.name}>{category.name}</option>
))
    // var itemsArr =[]
    // function GetCategoryProducts(e){
    //     for(let category of CategoriesArr ){
    //         // console.log(category);
            
    //         if(category.name === e.target.value){
    //             // console.log(category,"ss");
    //             for(let product of ProductArr){
    //                 console.log(product);
    //                 if(category.items.includes(parseInt(product.id))){
    //                     itemsArr.push(product)
    //                 }
    //         }
    //     }
    // }}
    function CategoryChanged(e){
        // console.log("hi");
    var itemsArr =[]
    function GetCategoryProducts(e){
        // console.log(e.target.value);
        if(e.target.value === 'All'){
            itemsArr = ProductArr
        }else{
        for(let category of CategoriesArr ){
            // console.log(category);
            
            if(category.name === e.target.value){
                // console.log(category,"ss");
                for(let product of ProductArr){
                    // console.log(product);
                    if(category.items.includes(parseInt(product.id))){
                        itemsArr.push(product)
                    }
            }
        }
    }}}
    GetCategoryProducts(e)
    SetProductRow(itemsArr.map((product)=>
    <ProductDetailsRow key={product.id} {...product} />
))
    }

    function ProductSearch(e){
        const itemsArr = []
    for(let product of ProductArr){
        // console.log(product);
        var pname = product.name.toLowerCase()
        var squery = e.target.value.toLowerCase()
        // console.log(pname,squery);
        if(pname.startsWith(squery)){
            itemsArr.push(product)
        }}
        SetProductRow(itemsArr.map((product)=>
    <ProductDetailsRow key={product.id} {...product} />
))
    }
    // const ProductRow = ProductArr.map((product)=>
    //     <ProductDetailsRow {...product} />
    // )

    return ( 
    <>
    <div className="admin_sections" >

    <div className="admnsectitle" >Products</div>

        <div className='slctbtnbtn' >
            
            <select name="" id="" onChange={CategoryChanged} >
                <option value="All">All</option>
                {Categories}
            </select>

            <Link to={`/admin_dashboard/products/category=${0}`} className="edit-data" ><i className="bi bi-pencil-square"></i></Link>
            <div className='grdend' >

            <Link to={'/admin_dashboard/products/add_product'} className="bluewhtbtn"><i className="bi bi-plus"></i>Add Product</Link>
            <Link  to={'/admin_dashboard/products/add_category'} className="bluewhtbtn"><i className="bi bi-plus"></i>Add Category</Link>
            </div>

        </div>
        <div className="fwsearchbar">
          <i className="bi bi-search"></i>
          <input type="text" name="" id="" onInput={ProductSearch} placeholder="search products" />
        </div>

        <div className='PrdctTable-head tblehead' >
            <span>Id</span>
            <span>Product</span>
            <span>Selling Price</span>
            <span>In Stocks</span>
        </div>

        <div className='Prdtct-details-table-cont ' >
            {ProductRow}
        </div>


        <Routes>
            <Route path={"/add_product"} element={<AddProduct/>} />
            <Route path={"/add_category"} element={<AddCategory/>} />
            <Route path={"/product=:slug"} element={<ViewProduct />} />
            
        </Routes>

    </div>
    
    </>
     );
}

export default ProductsAn;